import React, { useEffect, useState } from 'react';
import { IonButton, IonContent, IonFooter, IonHeader, IonLabel, IonTitle, IonToolbar, useIonToast } from '@ionic/react';
import { IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonIcon } from '@ionic/react';
import { cashOutline } from 'ionicons/icons';
import Nav from '../components/nav';
import depositosService from '../services/depositosService';

const Operacoes = () => {




const [ VerDepositos, SetVerDepositos] =  useState([]);
    useEffect(() => {
      AllDepsitos()
    }, [])  
const AllDepsitos = async () => {
  depositosService.getDepositos()
  .then((response) => {
  
    // console.log(response.data.Depositos)
    SetVerDepositos(response.data.Depositos)
  })
  .catch((error) => {
  
    
    // console.log(error)
    // console.log("errado")
  })
  setTimeout(AllDepsitos,1000)

}



  return (
  <>    
<Nav/>
<IonContent fullscreen={false}>

      {
          VerDepositos.map((deposito) => (
      <IonCard color='success' key={deposito.id}>
        <IonCardHeader>
          <IonLabel>Recebido</IonLabel>
          <IonCardTitle> <IonIcon icon={cashOutline} /> {deposito.valor} kzs</IonCardTitle>
          <IonLabel>de {deposito.usuario}</IonLabel>
      </IonCardHeader>
      </IonCard>
         ))
        }
     

    
      </IonContent>
      <IonFooter>
          <IonToolbar>
            <IonTitle>KIPAGA</IonTitle>
          </IonToolbar>
        </IonFooter>
    {/* <IonButton expand="block" onClick={() => presentToast('top')}>Fazer Transferncia</IonButton> */}
  </>

  );
};

export default Operacoes;