import React, { useState } from 'react';
import { IonButton, IonCardContent, IonCol, IonContent, IonGrid, IonHeader, IonImg, IonInput, IonItem, IonLabel, IonNavLink, IonRow, IonTitle, IonToolbar, useIonToast } from '@ionic/react';
import { IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonIcon } from '@ionic/react';
import { cashOutline, cash, star, send, sendOutline, arrowUp, arrowDown, arrowDownCircle, qrCode, card, returnUpBack, playOutline, personAdd } from 'ionicons/icons';
import Logo from '../imgs/logo.png' 
import depositosService from '../services/depositosService';
import '../styles/login.css';
import swal from 'sweetalert';
 
import jwt from 'jwt-decode' // import dependency
import { Redirect, } from 'react-router';
import { Link, Route, Routes } from 'react-router-dom';

const Login = () => {

  const [username, setUsername] = useState('');
  const [password, setPassword ]= useState('');
  const [isError, setError ]= useState(false);
  const [isLoggedIn, setLogin] = useState(false);
 

  //Botao Salvar
  const saveUpdateCooperador = async (e) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append("username", username);
    formData.append("password", password);
// console.log(username);
// console.log(password);
    await depositosService.getToken(formData)
        .then((response) => {
           const token = response.data.access;
          //  console.log(token);

      const user = jwt(token); // decode your token here
       localStorage.setItem('token', token);
       const decode1 = jwt(token);
      //  console.log(decode1);
       localStorage.setItem('id', decode1.user_id);
       setLogin(true);
          // console.log(response.data);

          swal({
            title: "Good job!",
            text: "You clicked the button!",
            icon: "success",
            button: false,

            timer: 1500
          });

           // retrievecooperadores();
           return <Redirect exact to="/inicio/" />;

        })
        .catch((error) => {

          // console.log(error);
          swal({
            title: "Erro!",
            text: "Por favor verifique suas credenciais!"+error,
            icon: "error",
            button: false,

            timer: 1500

          });

          
             });

  };
  return (
    <>
  {/* baner imagem no visor */}
  <IonImg src={Logo} style={{ width:'60%', margin:'20%' }}></IonImg>
        <IonCard>
        <form
        action='/inicio'
                                    encType="multipart/form-data"

        onSubmit={(e) => saveUpdateCooperador(e)}
      >          <IonCardHeader>     
                     <IonLabel position="stacked">Usurio </IonLabel>
                  
                <input 
                                  className='form-control'

                 type="text"
                   placeholder="user@mail.com" 
                 name="username" 
                  onChange={(e) => setUsername(e.target.value)}
                 required
                 /> 
                    <IonLabel position="stacked">Password </IonLabel>
                  
                <input 
                                  className='form-control'

                 type="password"
                   placeholder="*******" 
                   name="password" 
                   onChange={(e) => setPassword(e.target.value)}
                 required
                 />
 
                 
  <br />
                  <IonButton type="submit" expand="block">Entrar</IonButton>

                 <br />
                {/* <IonLabel style={{}} color='primary'>Criar conta</IonLabel>
                <br />
               
 */}


                <IonButton expand="block"  href='/cadastro'> Criar Conta <IonIcon icon={personAdd}></IonIcon></IonButton>
                <IonLabel style={{textAlign:"center"}} className='text-center' color='primary'>Problemas com o login?</IonLabel>
            </IonCardHeader>
          </form>

        </IonCard>
      </>

  );
};

export default Login;
